<template>
  <div class="dashboard-box">
    <h1>工作台</h1>
    <div>
      <h3>文件上传组件测试</h3>
      <g-file-upload />
      <p>文件上传组件已添加</p>
    </div>
    <g-btn text="测试网络请求" @callback="getData"></g-btn>
    <p>{{ data }}</p>

    <div class="dashboard-content">
      <div class="widget">
        <h2>欢迎回来！</h2>
        <p>这是您的工作台页面。</p>
      </div>
      <div class="widget" v-for="item in 3">
        <h2>最近活动</h2>
        <ul>
          <li>完成了项目A的开发任务</li>
          <li>与客户B进行了会议</li>
          <li>提交了报告C</li>
        </ul>
      </div>
    </div>
     <!-- 基本使用 -->
     <g-anime 
      :config="fadeInConfig" 
      @animation-end="onAnimationEnd">
      <div class="box">淡入效果</div>
    </g-anime>

      <!-- 手动控制动画 -->
      <g-anime 
      ref="slideAnimation"
      :config="slideInConfig"
      trigger="manual"
      :autoplay="false">
      <div class="box">滑入效果</div>
    </g-anime>
    
    <button @click="playSlideAnimation">播放滑入动画</button>
    
    <!-- 进入视口时触发动画 -->
    <g-anime 
      :config="bounceConfig"
      trigger="visible">
      <div class="box">弹跳效果（滚动到此处时触发）</div>
    </g-anime>
    
    <!-- 链式动画 -->
    <g-anime 
      ref="chainAnimation"
      :config="chainAnimationConfig">
      <div class="box">链式动画</div>
    </g-anime>
    <!-- 快捷方式模块 -->
    <div class="quick-content">
      <div class="card-title">快捷方式</div>
      <div class="dashboard-quick">
        <div class="widget-quick" v-for="item in 12">
          <!-- 首页图标 -->
          <div class="img">
            <i
              title="首页"
              class="ri-home-line"
              style="
                color: #fff;
                cursor: pointer;
                font-size: 22px;
                font-weight: bold;
                text-align: center;
              "
            ></i>
          </div>

          <span>快捷方式</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { userService } from "@/service";
export default {
  name: "Dashboard",

  // Add any necessary component options or logic here
  data() {
    return {
      data: null,
      fadeInConfig: {
        opacity: [0, 1],
        duration: 5000,
        easing: 'easeInOutQuad'
      },
      slideInConfig: {
        translateX: [-100, 0],
        opacity: [0, 1],
        duration: 800,
        easing: 'spring(1, 80, 10, 0)'
      },
      bounceConfig: {
        scale: [0.5, 1],
        opacity: [0, 1],
        duration: 1500,
        elasticity: 500
      },
      chainAnimationConfig: {
        translateY: [
          { value: -20, duration: 500 },
          { value: 0, duration: 500 }
        ],
        rotate: {
          value: '1turn',
          duration: 5000,
          delay: 500
        },
        backgroundColor: {
          value: '#FF0000',
          duration: 800,
          delay: 1500
        }
      }
    };
  },
  methods: {
    onAnimationEnd() {
      console.log('动画完成！');
    },
    playSlideAnimation() {
      this.$refs.slideAnimation.play();
    },
    getData() {
      userService.getStatusData().then((res) => {
        console.log("请求成功", res);
        this.data = res;
      });
    },
  },
};
</script>

<style lang="less" scoped>
.dashboard-content {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-bottom: 8px;
}

.widget {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
}

.widget h2 {
  margin-bottom: 10px;
}

.widget ul {
  padding-left: 20px;
}

.quick-content {
  background-color: #fff;
  .card-title {
    padding: 0 24px;
    height: 50px;
    line-height: 50px;
    box-sizing: border-box;
    border-bottom: #f0f0f0 1px solid;
    font-size: 16px;
    font-weight: 500;
  }
}

.dashboard-quick {
  width: 100%;
  flex-wrap: wrap;
  display: flex;
  padding: 24px;
}

.widget-quick {
  width: 25%;
  height: 60px;
  align-items: center;
  border-radius: 5px;
  color: #000000;
  display: flex;
  font-size: 14px;
  line-height: 22.001px;
  cursor: pointer;
  &:hover {
    background-color: #f0f0f0;
  }
  .img {
    width: 42px;
    height: 42px;
    border-radius: 10px;
    display: flex;
    font-size: 12px;
    margin: 0px 8px 0px 10px;
    align-items: center;
    justify-content: center;

    background-color: @primary-color;
  }
}

.dashboard-box {
  height: 100%;
  overflow: auto;
  padding: 10px;
  box-sizing: border-box;
}
</style>
