import { get,post } from '@/utils/axios/axios';

const getStatusData = () => {
  return get({
        url: '/getUser',
  });
};
const postData = (data) => {
  return post({
    url: '/goapi/soft/add',
    data,
    showError: false,
  });
};

export default {
    getStatusData,
    postData
}
